import { BrowserModule } from '@angular/platform-browser';
import { NgModule, APP_INITIALIZER } from '@angular/core';

import { RoutingModule } from './app.routing';
import { AppComponent } from './app.component';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { ToastNoAnimationModule } from 'ngx-toastr';

import { services } from './core';
import { APP_BASE_HREF } from '@angular/common';
import { HttpDefaultsInterceptor } from './core/interceptors/httpDefaultsInterceptor/app.interceptor';
import { init_app } from './core/services/lifecycle/lifecycle.factory';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    RoutingModule,
    FormsModule,
    ReactiveFormsModule,
    ToastNoAnimationModule.forRoot()
  ],
  providers: [
    [{ provide: services.LifeCycleService, useClass: services.LifeCycleService}],
    [{ provide: APP_INITIALIZER, useFactory: init_app, deps: [services.LifeCycleService], multi: true }],
	//[{ provide: APP_BASE_HREF, useValue: window['_app_base'] || '/' }],
	[{ provide: HTTP_INTERCEPTORS, useClass: HttpDefaultsInterceptor, multi: true }],
    [{ provide: services.EnvironmentService, useClass: services.EnvironmentService }],
	[{ provide: services.LoggingService, useClass: services.LoggingService }],
	[{ provide: services.AuthenticationService, useClass: services.AuthenticationService}],
    //[{ provide: HTTP_INTERCEPTORS, useClass: interceptors.CSRFInterceptor, multi: true }],
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }

function use(values): Array<any>{
	return Object.values(values);
}
