import { Injectable } from '@angular/core';
import { EnvironmentService } from '../environment/environment.service';


/**
 * @author Schwada
 * @since 9.1.2019
 * 
 * Simple service for logging. Default implementation safely writes the message
 * into the browser's console based on the environment.
 * The main purpose of this service is to simplify debugging and troubleshooting. 
 */


@Injectable({
	providedIn: 'root'
})
export class LoggingService {

	private enabled: boolean = false;
	private timestamps: boolean = false;
	private traceEnabled: boolean = false; //TODO

	constructor(private envService: EnvironmentService) {
		this.enabled = envService.read('logEnabled');
  	}
	 
	get info() { return this.log('info',); }
	get warn() { return this.log('warn'); }
	get error() { return this.log('error'); }

	/**
	 * General logging method that extends 
	 * the console function by checking if
	 * debug is enabled for this environment
	 * 
	 * @param type 
	 */
	public log(type: string){
		if (this.enabled) {
			if(this.timestamps == true){
				let timestamp = "[ " + type.toUpperCase() + " " + (new Date).toLocaleTimeString() + " ]";
				return console[type].bind(console,'%s', timestamp);
			}else{
				return console[type].bind(console);
			}
		}
	}

}
