import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '../authentication/authentication.service';
import { LoggingService } from '../logging/logging.service';
import { CookiesService } from '../cookies/cookies.service';
import { Authinfo } from 'src/app/shared/models/Authinfo';
import { interval } from 'rxjs';
import { ToastrService } from 'ngx-toastr';

/**
 * @author Schwada
 * @since 9.1.2019
 * 
 */


@Injectable({
	providedIn: 'root'
})
export class LifeCycleService {

    private appInitialized: boolean = false;

    constructor(
        private authentication: AuthenticationService,
        private cookies: CookiesService,
    ) {}

    public get isAppInitialized(){ return this.appInitialized; }

    async initializeApp(): Promise<any> {
        return await new Promise((resolve, reject) => {

            //dev
            //resolve();

            if(!this.authentication._authenticated){
                // check if user is actually not logged in
                let auth: string = this.cookies.get("authinfo");
                if( auth != "" ){
                    let authinfo: Authinfo = Authinfo.fromString(auth);
                    // got authinfo ...check if still valid
                    this.authentication.checkSession(authinfo).then((data) => {
                        this.authentication.authenticate(null,data,authinfo).then((data) => {
                            this.appInitialized = true;
                            // start checking if still active
                            this.checkActivity(); 
                            resolve();
                            
                        })
                    },(error) =>{
                        // is no longer valid so delete identity
                        this.authentication.dismissExpiredIdentity();
                        this.appInitialized = true;
                        resolve();
                        //console.log(error);
                    });

                }else{
                    console.log("no authdata");
                    this.appInitialized = true;
                    this.checkActivity();
                    resolve();
                }
            }
        });
    }

    public checkActivity(){
        if(this.authentication._authenticated){
            interval(800000).subscribe((value) => { 
                this.authentication.checkSession().then((data) => {
                    //log is still active
                },(error) => {
                    this.authentication.dismissExpiredIdentity();
                }) 
            });
        }
    }


    //TODO move to error handling service    
    // public unauthorizedAccess(){
    //     this.toast.warning("oh no!", "Nemáte práva");
    // }


}
