import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Authinfo } from '../../../shared/models/Authinfo';
import { CookiesService } from '../cookies/cookies.service';
import { EnvironmentService } from '../environment/environment.service';

/**
 * @author Schwada
 * @since  9.1.2019
 * 
 * 
 * TODO FIX SHITTY AUTHENTICATION
 * 
 * This essential service contains all the authentication 
 * logic ( password validation, session initialization... )   
 * aswell as stores information about currently authenticated user
 */


@Injectable({
	providedIn: 'root'
})
export class AuthenticationService {

    constructor(
        public http: HttpClient,
        public envService: EnvironmentService,
        public cookies: CookiesService
    ){
        // dev this.authenticated = true;
    }
    
    /* User authinfo */
    private authinfo: Authinfo = null;
    /* User information was compared to credentials */
    private authenticated: boolean = false; 
    // Credentials - either email/username/id or 2fa data 
    private credentials: object = {};

    

    /**
     * Returns promise which calls login on REST
     * 
     * @method
     * @param {string} username
     * @param {string} email
     * @param {string} password 
     * @return {Promise<{}>}
     */
    public login(username: string, email: string, password: string): any{
        // let headers = { authorization: "Basic " + btoa(username + ":" + password) };
        let credentials = { "name": username, "pass": password };
        this.credentials = credentials;

        return new Promise((resolve,reject) => {
            this.http.post(this.envService.read('apiUrl') +'/'+ window['instance_id'] + '/login', credentials).subscribe(resolve, reject);
        });
        
    }
    
    /**
     * Returns promise which checks in the backend that session is still 
     * alive, returns current user's identity
     *
     * @method
     * @return {object} current user's identity
     */
    checkSession(auth?: Authinfo): any {
        let authinfo = null;
        if(auth == undefined && this.authinfo != null){ 
            authinfo = this.authinfo.get();
        }else{
            authinfo = auth;
        }
        
        return new Promise((resolve,reject) => {
            this.http.post(this.envService.read('apiUrl') + "/me", authinfo).subscribe(resolve, reject);
        });    
    }
    
    /**
     * Returns promise which calls logout on REST
     * 
     * @method
     * @return {Observable<any>}
     */
    public logout(): any{
        // return new Promise((resolve,reject) => {
        //     this.http.post(this.envService.read('apiUrl') +'/'+ window['instance_id'] + '/login', credentials).subscribe(resolve, reject);
        // });

    }
    
    /**
     * Returns promise which sets user's new authinfo from credentials
     * saves the authinfo id to cookies
     * 
     * @method authenticate
     * @param {string} authdata 
     * @return {Promise<{}>}
     */
    public authenticate(authdata: string, credentials?: object, authinfo? : Authinfo): Promise<{}> {
        let authenticate = new Promise((resolve,reject) => {
            console.log(this.cookies.get("authinfo"));
            console.log(this.cookies.get("authinfo") == undefined);
            if(this.credentials != null && authdata != null) {
                this.credentials['id'] =  authdata.split('|')[0]; 
                let parsed = new Authinfo(this.credentials['name'],this.credentials['email'], authdata.split('|')[1])
                this.authinfo = parsed;
                this.authenticated = this._authinfo != null;
                this.cookies.set("authinfo", parsed._name + "|" + parsed._email + "|" + parsed._token , 1);
                resolve(true);
            }else{
                if(credentials != undefined && credentials['id'] != undefined && authinfo != undefined){
                    this.credentials['id'] = credentials['id'];
                    this.authinfo = authinfo;
                    this.authenticated = true;
                    this.cookies.set("authinfo", authinfo._name + "|" + authinfo._email + "|" + authinfo._token , 1);
                    resolve(true);
                }else{
                    reject();
                } 
            }
        });

        return authenticate;
    }
    
    /**
     * Returns promise which erases identity from 
     * this service ( aka. logs the user out from angular app )
     * 
     * @method
     * @return {Promise<{}>} 
     */
    public dismissExpiredIdentity(): Promise<{}> {
        let dismissIdentity = new Promise((resolve) => {
            this.authinfo = null;
            this.credentials = null;
            this.authenticated = false;
            this.cookies.delete("authinfo");
            resolve(true);
        });

        return dismissIdentity;
    }

    /**
     * Returns ( a copy of ) data of current
     * angular authinfo ( name+token )
     * 
     * @method
     * @return {Authinfo} authinfo
     */
    get _authinfo(): any {
        return this.authinfo;
    }
    
    /**
     * Returns ( a copy of ) data of current
     * angular identity ( name...etc )
     * 
     * @method
     * @return {object} credentials
     */
    get _credentials(): object {
        return this.credentials;
    }

    /**
     * Returns ( a copy of ) data of current
     * angular authentication state (
     * 
     * @method
     * @return {boolean}
     */
    get _authenticated(): boolean {
        return this.authenticated;
    }


}


