import { Injectable, Inject } from "@angular/core";
import { DOCUMENT } from '@angular/common'

/**
 * @author Schwada
 * @since  9.1.2019
 * 
 * This essential service contains all the authentication 
 * logic ( password validation, session initialization... )   
 * aswell as stores information about currently authenticated user
 */


@Injectable({
	providedIn: 'root'
})
export class CookiesService {

	constructor(
		@Inject(DOCUMENT) private document: Document
	) {
		this.cookiesEnabled = this.userAcceptedCookies();
	}

	/* User has accepted the use of cookies */
	private cookiesEnabled: boolean = true;


	
    /**
     * Sets that the user has
     * agreed to the use of cookies
     * 
     * @method
     * @param value 
     * @returns {void}
     */
	public setAcceptedCookies(value: boolean): void {
		var now = new Date();
		var exp = new Date(now.getFullYear() + 1, now.getMonth(), now.getDate());
		this.cookiesEnabled = value;
		this.set('cookies-allowed', "" + value , exp);
	}

    /**
     * Cheks if user has already
     * agreed to the use of cookies
     * 
     * @method
     * @returns {boolean}
     */
	public userAcceptedCookies(): boolean {
		return this.check('cookies-allowed');
	}

    /**
     * Asks if cookies
     * are enabled in the browser
     * 
     * @method
     * @returns {boolean}
     */
	public browserEnabledCookies(): boolean {
		return navigator.cookieEnabled;
	}



	/* Actual cookie manipulation below */

	/**
	 * Check if the cookie exists
	 * 
	 * @param name Cookie name
	 * @returns {boolean}
	 */
	check(name: string): boolean {
		name = encodeURIComponent(name);
		const regExp: RegExp = this.getCookieRegExp(name);
		const exists: boolean = regExp.test(this.document.cookie);

		return exists;
	}

	/**
	 * Get the cookie value
	 * 
	 * @param name Cookie name
	 * @returns {any}
	 */
	get(name: string): any {
		if (this.check(name)) {
			name = encodeURIComponent(name);
			const regExp: RegExp = this.getCookieRegExp(name);
			const result: RegExpExecArray = regExp.exec(this.document.cookie);
			
			return decodeURIComponent(result[1]);
		} else {
			return '';
		}
	}

	/**
	 * Get all the cookies
	 * 
	 * @returns {object}
	 */
	getAll(): object {
		const cookies: object = {};
		if (this.document.cookie && this.document.cookie !== '') {
			const split: Array<string> = this.document.cookie.split(';');
			for (let i = 0; i < split.length; i += 1) {
				const currentCookie: Array<string> = split[i].split('=');
				currentCookie[0] = currentCookie[0].replace(/^ /, '');
				cookies[decodeURIComponent(currentCookie[0])] = decodeURIComponent(currentCookie[1]);
			}
		}

		return cookies;
	}

	/**
	 * Creates a cookie
	 * 
	 * @param {string} name Cookie name
	 * @param {string} value Cookie value
	 * @param { number | Date } expires Number of days until the cookies expires or an actual Date
	 */
	public set(name: string, value: string, expires?: number | Date, path?: string): void {
		let cookieString: string = encodeURIComponent(name) + '=' + encodeURIComponent(value) + ';';
		if (expires) {
			if (typeof expires === 'number') {
				const dateExpires: Date = new Date(new Date().getTime() + expires * 1000 * 60 * 60 * 24);
				cookieString += 'expires=' + dateExpires.toUTCString() + ';';
			} else {
				cookieString += 'expires=' + expires.toUTCString() + ';';
			}
		}
		cookieString += "; path=/";

		this.document.cookie = cookieString;
	}

	/**
	 * Deletes a cookie
	 * 
	 * @method
	 * @param {string} name Cookie name
	 * @return {void} 
	 */
	public delete(name: string): void {
		this.set(name, '', new Date('Thu, 01 Jan 1970 00:00:01 GMT'));
	}

	/**
	 * Clear all cookies
	 * 
	 * @method
	 * @return {void}
	 */
	public clear(path?: string, domain?: string): void {
		const cookies: object = this.getAll();
		for (const cookieName in cookies) {
			if (cookies.hasOwnProperty(cookieName)) {
				this.delete(cookieName);
			}
		}
	}


	/* Private instance methods below */

	/**
	 * @method
     * @param {string} cookie 
     * @returns {RegExp}
     */
	private getCookieRegExp(name: string): RegExp {
		const escapedName: string = name.replace(/([\[\]\{\}\(\)\|\=\;\+\?\,\.\*\^\$])/ig, '\\$1');
		return new RegExp('(?:^' + escapedName + '|;\\s*' + escapedName + ')=(.*?)(?:;|$)', 'g');
	}

}