
/**
 * @author schwada
 * @since 9.1.2019
 * 
 * A wrapper class around the authentication data
 * available for different providers and authentication methods
 * in the central backend and frontend aswell.
 */

export class Authinfo {
    
    private name: string;
    private email: string;
    private token: string;

    constructor (name:string,email:string,token:string) {
        this.name = name;
        this.email = email;
        this.token = token;
        
    }


    public static fromString(authinfo: string): Authinfo {
        let data: string[] = authinfo.split("|");
        for (let index = 0; index < data.length; index++) {
            data[index] = (data[index] == "undefined") ? null : data[index];  
        }
        return new Authinfo(data[0],data[1],data[2]);
    }


    get _name(): string { return this.name; }
    set _name(name: string) { this.name = name; }

    get _email(): string { return this.email; }
    set _email(email: string) { this.email = email; }

    get _token(): string { return this.token; }
    set _token(token: string) { this.token = token; }


    get (): object {
        let auth_info:object = {};
        if(this.name != null){
            auth_info['name'] = this.name;
        }else if(this.email != null){
            auth_info['email'] = this.email;
        }else{
            return {};
        }
        auth_info['token'] = this.token;
        return auth_info ;
    }

}